import {
    SET_MESSAGE,
    SET_DATA,

  } from '../actions/ApplicationActions';
  
  const initialState = {
    message: {
      text: ""
    },
    drawer: {

    },
    data: {

    }
  };
  
  const EcommerceReducer = function (state = initialState, action) {
    switch (action.type) {
      case SET_MESSAGE: {

        return {
          ...state,
          message: {
            text: action?.payload.text
          },
        };
      }
      case SET_DATA: {
        const { ns, data } = action?.payload
        console.log(action)
        return {
          ...state,
          data: {
            ...state.data,
            [ns]: data
          }
        };
      }
      default: {
        return {
          ...state,
        };
      }
    }
  };
  
  export default EcommerceReducer;
  