export const SET_MESSAGE = 'SET_MESSAGE';
export const SET_DATA = 'SET_DATA';

export const setApplicationMessage = (payload) => {
    return {
        type: SET_MESSAGE,
        payload
      }
}

export const setData = (ns, data) => {
  return {
      type: SET_DATA,
      payload: {
        ns,
        data
      }

    }
}


